import React from "react";
import "./Button.css";

const Button = props =>
  props.dummy
    ? <div className="Button">
        {props.children}
      </div>
    : <a
        className="Button"
        href={props.href}
        style={{
          display:
            props.href !== undefined && props.href.length < 1
              ? "none"
              : "inline-block"
        }}
      >
        {props.children}
      </a>;

export default Button;



